<template>
    <main>
        <transition-group name="list" tag="div" class="list" @before-leave="beforeLeave">
            <md-card v-for="(i, index) in sliced" :key="i.id" :style="{ 'background-image': 'url(' + i.image.original + ')' }">

                <md-card-media :style="{ 'background-image': 'url(' + i.image.original + ')' }">
                </md-card-media>
                <md-card-header class="md-card-header">
                    <md-card-header-text>
                        <div class="md-title">{{ i.name }}</div>
                        <div class="md-subhead">{{i.genres[0]}} <span v-if="i.genres[1]">\</span> {{i.genres[1]}} <span v-if="i.genres[2]">\</span> {{i.genres[2]}}</div>
                    </md-card-header-text>
                    <md-menu md-size="medium" md-direction="bottom-end">
                        <md-button class="md-icon-button" md-menu-trigger>
                            <md-icon>more_vert</md-icon>
                        </md-button>

                        <md-menu-content>
                            <md-menu-item @click="imdb(index)">
                                <span>IMDB</span>
                                <md-icon>link</md-icon>
                            </md-menu-item>

                            <md-menu-item @click="removeMovie(index)">
                                <span>Ta bort</span>
                                <md-icon>delete</md-icon>
                            </md-menu-item>
                        </md-menu-content>
                    </md-menu>

                </md-card-header>

                <md-card-content v-html="i.summary">
                </md-card-content>
            </md-card>
        </transition-group>
    </main>
</template>

<script>
    import axios from 'axios'
    export default {
        name: 'filmer',
        data() {
            return {
                info: '',
                offset: 4, // items to display after scroll
                display: 8, // initial items
                trigger: 600,
            }
        },
        computed: {
            sliced() {
                return this.info.slice(0, this.display);
            },
        },
        methods: {
            addMovie: function() {
                this.movies.push({
                    title: this.title,
                    imdb: this.imdb,
                    description: this.description
                })
                this.title = "";
                this.imdb = "";
                this.description = "";

            },
            removeMovie: function(index) {
                this.info.splice(index, 1);
            },
            imdb: function(index) {
                window.open('http://imdb.com/title/' + this.info[index].externals.imdb, '_blank');
            },
            beforeLeave(el) {
                const {
                    marginLeft,
                    marginTop,
                    width,
                    height
                } = window.getComputedStyle(el)
                el.style.left = `${el.offsetLeft - parseFloat(marginLeft, 10)}px`
                el.style.top = `${el.offsetTop - parseFloat(marginTop, 10)}px`
                el.style.width = width
                el.style.height = height
            },
            scroll() {
                window.onscroll = ev => {
                    if (
                        window.innerHeight + window.scrollY >=
                        (document.body.offsetHeight - this.trigger)
                    ) {
                        if (this.display < this.info.length) {
                            this.display = this.display + this.offset;
                        } else {
                            this.end = true;
                        }
                    }
                };
            },
        },
        mounted() {
            axios
                .get('http://api.tvmaze.com/show/')
                .then(response => (this.info = response.data.splice(0, 150)));
            this.scroll();
        }
    }

</script>

